        // 返回本地缓存的值
        getCookie: function(cname){
            var name = cname + "=";
            var ca = document.cookie.split(';');
            for(var i=0; i<ca.length; i++) {
                var c = ca[i].trim();
                if (c.indexOf(name)==0) { return c.substring(name.length,c.length); }
            }
            return "";
        },
        // 返回本地缓存的值
        returnStorageVal: function(name, val){
            var val = val;
            var newVal = window.localStorage.getItem(name, val);
            return newVal;
        },
        // 设置本地缓存值  param : name - key string   val - value  - object,number,string
        setStorageVal: function(name, val){
            var val = val;
            var newVal = window.localStorage.setItem(name, val);
            return newVal;
        },
        clearStorageVal: function(name, val){
            window.localStorage.removeItem(name);
        },
        // ajax(dom,url,param,callback,type){
        ajax(jsonObj){
            var jsonObj = jsonObj;
            var dom = jsonObj.dom || "";
            var type = jsonObj.type || "get";
            var url = jsonObj.url || "";
            var param = jsonObj.data || {};
            var success = jsonObj.success || function(){};
            var isLoding = jsonObj.isLoding || false;
            var isLodingTit = jsonObj.isLodingTit || "数据加载中";
            var error = jsonObj.error || function(response){
                // console.log('response.data',response)
                if(response.data) {
                    dom.$vux.toast.show({
                        type: 'text',
                        text: response.data.msg || "请求数据错误，联系管理员",
                        position: 'middle'
                    })
                } else {
                    dom.$vux.toast.show({
                        type: 'text',
                        text: response.msg || "网络可能存在问题，请联系管理员",
                        position: 'middle'
                    })
                }
            };
            param.access_token = window.localStorage.getItem("access_token") || "";
            if(dom == ""){
                dom.$vux.toast.show({
                    type: 'text',
                    text: "this必填",
                    position: 'middle'
                })
                return;
            }
            if(url == ""){
                dom.$vux.toast.show({
                    type: 'text',
                    text: "请求地址必填",
                    position: 'middle'
                })
                return;
            }
            if(isLoding) {
                dom.$vux.loading.show({
                    text: isLodingTit
                })
            }
            if(type == "get") {
                dom.$ajax.get(url,{
                    params: param
                }).then(response =>{
                    if(response.data.code == 0) {
                        var data = response.data.data;
                        success(data,response.data);
                    } else {
                        if(response.data.code == 1) {
                            // window.localStorage.removeItem("access_token");
                            // localStorage.setItem("thisUrl", window.location.href);
                            // dom.$router.push('/no');
                        } else {
                            error(response);
                        }
                    }
                    if(isLoding) {
                        setTimeout(()=>{
                            dom.$vux.loading.hide()
                        }, lodingTime)
                    }
                }).catch(response => {
                    error(response);
                    if(isLoding) {
                        setTimeout(()=>{
                            dom.$vux.loading.hide()
                        }, lodingTime)
                    }
                });
            } else {
                dom.$ajax.post(url, dom.$qs.stringify(param)).then(response => {
                    if(response.data.code == 0) {
                        var data = response.data.data;
                        success(data,response.data);
                    } else {
                        if(response.data.code == 1 && response.data.msg == '用户登录验证失败') {
                            dom.$router.push('/login');
                            error(response);
                        }  else {
                            error(response);
                        }
                    }
                    if(isLoding) {
                        setTimeout(()=>{
                            dom.$vux.loading.hide()
                        }, lodingTime)
                    }
                }).catch(response => {
                    error(response)
                    if(isLoding) {
                        setTimeout(()=>{
                            dom.$vux.loading.hide()
                        }, lodingTime)
                    }
                })
            }

        },
        getNowFormatDate(){
            var date = new Date();
            var seperator1 = "-";
            var seperator2 = ":";
            var month = date.getMonth() + 1;
            var strDate = date.getDate();
            if (month >= 1 && month <= 9) {
                month = "0" + month;
            }
            if (strDate >= 0 && strDate <= 9) {
                strDate = "0" + strDate;
            }
            var hours = date.getHours();
            if(hours >=0 && hours <=9){
                hours = "0" + hours;
            }
            var minutes = date.getMinutes();
            if(minutes >=0 && minutes <=9){
                minutes = "0" + minutes;
            }
            var seconds = date.getSeconds();
            if(seconds >=0 && seconds <=9){
                seconds = "0" + seconds;
            }
            var currentdate = date.getFullYear() + seperator1 + month + seperator1 + strDate
                + " " + hours + seperator2 + minutes
                + seperator2 + seconds;
            return currentdate;
        },

        // 连接服务端
        connect() {
            //创建websocket
            ws = new ReconnectingWebSocket("wss://demo.isaveu.cn:7272");
            // 当socket连接打开时，输入用户名
            ws.onopen = this.onopen;
            // 当有消息时根据消息类型显示不同信息
            ws.onmessage = this.onmessage;
            ws.onclose = function () {
                console.log("连接关闭，定时重连");
                this.connect();
            };
            ws.onerror = function () {
                console.log("出现错误");
            };
        },
